import React, { useEffect } from "react";
import News from "./Pages/News/News-page";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { useDispatch } from "react-redux";

import Header from "./components/Header/Header";
import Footer from "./components/Footer";
import StartScreen from "./Pages/StartScreen";
import ProfilePage from "./Pages/User/Profile/Profile-page";
import { auth } from "./redux/authActions";

function App() {
  const dispatch = useDispatch();

  useEffect( async () => {
   dispatch(auth());
  }, [dispatch]);

  return (
    <Router>
      <Header />
      <Switch>
        <Route exact path="/ALOY-IT-SCHOOL/" component={StartScreen} />
        <Route exact path="/ALOY-IT-SCHOOL/news" component={News} />
        <Route exact path="/ALOY-IT-SCHOOL/profile" component={ProfilePage} />
        <Route exact path="/ALOY-IT-SCHOOL/rang" component={StartScreen} />
        <Route exact path="/ALOY-IT-SCHOOL/time-table" component={StartScreen} />
        <Route exact path="/ALOY-IT-SCHOOL/education-material" component={StartScreen} />
        <Route exact path="/ALOY-IT-SCHOOL/home-work" component={StartScreen} />
        <Route exact path="/ALOY-IT-SCHOOL/pay" component={StartScreen} />
      </Switch>
      <Footer />
    </Router>
  );
}

export default App;
